@charset "UTF-8";
/*
 * 皮肤：蓝色
 * ----------
 */
.skin-blue .backman-header .navbar {
  background-color: #3c8dbc; }
  .skin-blue .backman-header .navbar .sidebar-toggle {
    color: #ffffff; }
  .skin-blue .backman-header .navbar .sidebar-toggle:hover {
    color: #f6f6f6;
    background-color: #367fa9; }

.skin-blue .backman-header .logo {
  background-color: #367fa9;
  color: #ffffff;
  border-bottom: 0 solid transparent; }
  .skin-blue .backman-header .logo:hover {
    background-color: #357ca5; }

.skin-blue .wrapper,
.skin-blue .backman-sidebar {
  background-color: #222d32; }

.skin-blue .wrapper-content .container-fluid {
  border-top-color: #3c8dbc; }

.skin-blue .sidebar-menu > li.header {
  color: #4b646f;
  background: #1a2226; }

.skin-blue .sidebar-menu > li.home a {
  border-left-color: #222d32; }

.skin-blue .sidebar-menu > li.home:hover a, .skin-blue .sidebar-menu > li.home.cur a {
  color: #d5e9f2;
  background: #1e282c;
  border-left-color: #3c8dbc; }

.skin-blue .sidebar-menu > li.home.cur a span {
  color: #40b9ff; }

.skin-blue .sidebar-menu > li > a.treeview-title {
  color: #8aa4af;
  border-left: 3px solid transparent; }

.skin-blue .sidebar-menu > li:hover > a.treeview-title, .skin-blue .sidebar-menu > li.active > a.treeview-title {
  color: #ffffff;
  background: #1e282c;
  border-left-color: #3c8dbc; }

.skin-blue .sidebar-menu > li:hover .active, .skin-blue .sidebar-menu > li.active .active {
  overflow: visible !important; }
  .skin-blue .sidebar-menu > li:hover .active > a, .skin-blue .sidebar-menu > li.active .active > a {
    position: relative;
    color: #ffffff;
    background: #263338; }
    .skin-blue .sidebar-menu > li:hover .active > a:before, .skin-blue .sidebar-menu > li.active .active > a:before {
      position: absolute;
      top: 0;
      left: -10px;
      width: 10px;
      height: 30px;
      content: ' ';
      background: #263338; }

.skin-blue .sidebar-menu > li > .treeview-menu {
  margin: 0 1px;
  background: #2c3b41; }

.skin-blue .sidebar-menu a {
  color: #b8c7ce; }
  .skin-blue .sidebar-menu a:hover {
    color: #d5e9f2;
    text-decoration: none; }
  .skin-blue .sidebar-menu a.cur {
    color: #40b9ff;
    text-shadow: #263338 1px 1px 0; }
