/**
 * 表格
 * ----
 */
table:not(.table) {
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
  background-color: transparent;
  border-collapse: separate;
  border-spacing: 0;
  border: #ddd 1px solid;
  border-left: 0;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px; }
  table:not(.table) caption + thead tr:first-child th,
  table:not(.table) caption + tbody tr:first-child th,
  table:not(.table) caption + tbody tr:first-child td,
  table:not(.table) colgroup + thead tr:first-child th,
  table:not(.table) colgroup + tbody tr:first-child th,
  table:not(.table) colgroup + tbody tr:first-child td,
  table:not(.table) thead:first-child tr:first-child th,
  table:not(.table) tbody:first-child tr:first-child th,
  table:not(.table) tbody:first-child tr:first-child td {
    border-top: 0; }
  table:not(.table) th {
    font-weight: bold; }
  table:not(.table) th,
  table:not(.table) td {
    padding: 8px;
    line-height: 20px;
    text-align: center;
    border: none;
    border-top: 1px solid #ddd;
    border-left: 1px solid #ddd; }
  table:not(.table) th[align="left"],
  table:not(.table) td[align="left"] {
    text-align: left; }
  table:not(.table) th[align="right"],
  table:not(.table) td[align="right"] {
    text-align: right; }
  table:not(.table) th[align="center"],
  table:not(.table) td[align="center"] {
    text-align: center; }
  table:not(.table).table-unborder,
  table:not(.table).table-unborder th,
  table:not(.table).table-unborder td {
    text-align: left;
    border: none; }

.table-responsive {
  margin-bottom: 20px; }
  .table-responsive table {
    margin-bottom: 0; }

@media (max-width: 767px) {
  .table-responsive table:not(.table) {
    border: none; }
    .table-responsive table:not(.table) tr th:first-child,
    .table-responsive table:not(.table) tr td:first-child {
