@charset "UTF-8";
/*
 * 头部
 * ----
 */
.backman-header {
  position: relative;
  max-height: 100px;
  z-index: 1030; }
  .backman-header .logo {
    display: block;
    float: left;
    width: 230px;
    height: 50px;
    padding: 0 15px;
    overflow: hidden;
    font-size: 20px;
    line-height: 50px;
    text-align: center;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-weight: 300;
    -webkit-transition: width 0.3s ease-in-out;
    -o-transition: width 0.3s ease-in-out;
    transition: width 0.3s ease-in-out; }
    .backman-header .logo img {
      width: 50px;
      margin: 0 3px;
      vertical-align: bottom; }
  .backman-header .navbar {
    min-height: 50px;
    margin-bottom: 0;
    margin-left: 230px;
    border-radius: 0;
    border: none;
    -webkit-transition: margin-left 0.3s ease-in-out;
    -o-transition: margin-left 0.3s ease-in-out;
    transition: margin-left 0.3s ease-in-out; }
  .backman-header .sidebar-toggle {
    display: none;
    float: left;
    margin: 0;
    padding: 15px;
    font-family: fontAwesome;
    color: #fff;
    background-color: transparent;
    background-image: none;
    border: 0; }
    .backman-header .sidebar-toggle:before {
      content: "\f0c9"; }
    .backman-header .sidebar-toggle:focus, .backman-header .sidebar-toggle:active {
      background: transparent; }
  .backman-header .navbar-right {
    float: right;
    padding-right: 20px;
    color: #fff;
    line-height: 50px; }
    .backman-header .navbar-right strong {
      margin-right: 5px; }
    .backman-header .navbar-right a {
      padding: 2px 8px;
      margin-left: 5px; }
      .backman-header .navbar-right a:active {
        padding: 3px 8px 1px; }

@media (max-width: 767px) {
  .backman-header {
    position: relative; }
    .backman-header .logo {
      width: 100%;
      float: none; }
    .backman-header .navbar {
      width: 100%;
      float: none;
      margin: 0; }
      .backman-header .navbar .sidebar-toggle {
        display: block; }
      .backman-header .navbar .navbar-right {
        padding-right: 0px; } }
